@use 'src/lib/ngx-editorjs-code-block.theme' as ngx-editorjs-code-block;

@import 'codemirror/lib/codemirror';
@import 'codemirror/theme/material-palenight';
@import 'codemirror/theme/material';
@import 'codemirror/addon/hint/show-hint.css';

@mixin theme($theme) {
  @include ngx-editorjs-code-block.theme($theme);
}

.CodeMirror {
  height: auto;
  font-family: 'Cascadia Code', 'Roboto Mono', monospace;
}